@media (min-width: 1921px) {
	.wrapper{
		max-width: $siteWidth*1.2 + px;
	}
}

@media (min-width: 980px) {
	.hide-desktop{
		display: none;
	}
}
@media (max-width: 980px) {
	.hide-mobile{
		display: none;
	}
}

@media (max-width: 1200px) {
	.tools__item{
		margin: 0;
	}

	.nav__link{
		margin: 0;
	}
}
@media (max-width: 980px) {
	.mobile-nav{
		flex: 0 0 15%;
		max-width: 15%;
	}
	.logo{
		flex: 0 0 70%;
		max-width: 70%;
	}
	.logo{
		text-align: center;
	}
}

